import Link from "next/link";
import { motion, AnimatePresence } from 'framer-motion';
import { X } from "lucide-react";

const sidebarItems =
    [
        { name: 'Dashboard', icon: <LayoutDashboard className="w-5 h-5" />, tab: 'dashboard' },
        { name: 'Events', icon: <CalendarCheck className="w-5 h-5" />, tab: 'events' },
        { name: 'Users', icon: <Users className="w-5 h-5" />, tab: 'users' },
        { name: 'Settings', icon: <Settings className="w-5 h-5" />, tab: 'settings' }
    ]

const Sidebar = ({ sidebarOpen, setSidebarOpen }: { sidebarOpen: boolean; setSidebarOpen: (open: boolean) => void }) => {
    return (
        <>
            {/* ✅ غطاء شفاف يغلق القائمة عند النقر خارجها */}
            {sidebarOpen && (
                <div
                    className="fixed inset-0 bg-black bg-opacity-50 z-40 md:hidden"
                    onClick={() => setSidebarOpen(false)}
                ></div>
            )}

            <aside
                className={`fixed inset-y-0 left-0 w-64 bg-blue-400 text-black font-semibold p-4 min-h-screen transition-transform duration-300 z-50
                        ${sidebarOpen ? "translate-x-0" : "-translate-x-full"} md:translate-x-0 md:static md:flex-shrink-0 `}
            >
                {/* ✅ زر الإغلاق في الشاشات الصغيرة */}
                <button className="md:hidden text-white mb-4" onClick={() => setSidebarOpen(false)}>
                    <X className="w-6 h-6" />
                </button>

                <h2 className="text-2xl font-bold mb-10 mt-2 cursor-pointer hover:text-gray-300">
                    <Link href="/dashboard">Admin Dashboard</Link>
                </h2>

                <ul className="space-y-6">
                    <li className="cursor-pointer hover:text-gray-300">
                        <Link href="/dashboard/appointments">Appointments Management</Link>
                    </li>
                    <li className="cursor-pointer hover:text-gray-300">
                        <Link href="/dashboard/patients">Patients Management</Link>
                    </li>
                    <li className="cursor-pointer hover:text-gray-300">
                        <Link href="/dashboard/doctor">Doctor Management</Link>
                    </li>
                    <li className="cursor-pointer hover:text-gray-300">
                        <Link href="/dataBooking">Booking Management</Link>
                    </li>
                    <li className="cursor-pointer hover:text-gray-300">
                        <Link href="#">Setting Management</Link>
                    </li>
                </ul>


                <motion.div
                    initial={{ x: -300, opacity: 0 }}
                    animate={{ x: 0, opacity: 1 }}
                    exit={{ x: -300, opacity: 0 }}
                    transition={{ type: 'spring', damping: 25 }}
                    className="w-64 relative bg-gradient-to-b from-indigo-900 to-indigo-800 text-white shadow-xl"
                >
                    <div className="p-4 flex items-center justify-between border-b border-indigo-700">
                        <motion.div
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            transition={{ delay: 0.2 }}
                            className="flex items-center space-x-2"
                        >
                            <div className="w-8 h-8 bg-white rounded-full flex items-center justify-center">
                                <School className="text-indigo-800 w-5 h-5" />
                            </div>
                            <span className="font-bold text-lg">UniJoy Admin</span>
                        </motion.div>
                    </div>

                    <nav className="p-4">
                        <motion.ul
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            transition={{ staggerChildren: 0.1 }}
                            className="space-y-2"
                        >
                            {sidebarItems.map((item) => (
                                <motion.li
                                    key={item.name}
                                    whileHover={{ scale: 1.02 }}
                                    whileTap={{ scale: 0.98 }}
                                >
                                    <button
                                        onClick={() => setActiveTab(item.tab)}
                                        className={`flex items-center w-full p-3 rounded-lg transition-colors ${activeTab === item.tab ? 'bg-indigo-700' : 'hover:bg-indigo-600'}`}
                                    >
                                        <span className="mr-3">{item.icon}</span>
                                        <span>{item.name}</span>
                                    </button>
                                </motion.li>
                            ))}
                        </motion.ul>
                    </nav>

                    <div className="absolute bottom-0 w-full p-4 border-t border-indigo-700">
                        <button className="flex items-center w-full p-3 rounded-lg hover:bg-indigo-600 transition-colors">
                            <LogOut className="mr-3 w-5 h-5" />
                            <span>Logout</span>
                        </button>
                    </div>
                </motion.div>
            </aside>
        </>
    );
};

export default Sidebar;